/* Enable smooth scrolling for the entire page */
html {
  scroll-behavior: smooth;
}

/* Main container for the About section */
.about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0a192f; // Dark blue background
  color: #8892b0; // Light blue text color
}

/* Wrapper for the main content (animation and text) */
.about-content-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 40px; 
  margin-bottom: 60px;
  width: 100%; // Full width
  padding: 100px 5%; // Add padding on the sides
  padding-top: 150px; // Extra padding on top for spacing
  padding-bottom: 0px; // Remove padding from the bottom
}

/* Lottie animation container */
.lottie-animation {
  flex: 0 0 auto;
  width: 400px;
  height: 400px; 
}

/* Text content styles */
.about-content {
  flex: 1;

  h2 {
    font-size: 2.5em;
    font-family: "Moderustic", sans-serif;
    color: #ccd6f6; // Lighter text color for heading
    margin-bottom: 20px;
    position: relative;
    
    /* Underline effect for the heading */
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -10px;
      width: 50px;
      height: 3px;
      background-color: #64ffda; // Teal underline
    }
  }

  p {
    font-size: 1.1em;
    font-family: "Outfit", system-ui;
    line-height: 1.6;
    color: #8892b0;
    margin-bottom: 15px;
  }
}

/* Skills section styles */
.skills-section {
  width: 90%;
  padding-bottom: 20px;
  

  h3 {
    font-size: 2em;
    font-family: "Moderustic", sans-serif;
    color: #ccd6f6;
    margin-bottom: 30px;
    text-align: center;
  }
}

/* Responsive design for tablets */
@media (max-width: 1024px) {
  .lottie-animation {
    width: 300px;
    height: 300px; 
  }
}

/* Responsive design for mobile devices */
@media (max-width: 768px) {
  .about-content-wrapper {
    flex-direction: column;
    padding-top: 80px;

    .about-content {
      p {
        padding-top: 20px;
      }
      h2 {
        display: flex;
        justify-content: center;

        /* Center the underline for mobile view */
        &::after {
          transform: translateX(-180%);
          position: absolute;
          left: 50%;
        }
      }
    }
  }

  /* Adjust layout for mobile view */
  .lottie-animation, .about-content {
    flex: 0 0 100%;
  }

  /* Responsive sizing for the Lottie animation */
  .lottie-animation {
    margin-bottom: 30px;
    width: 100%;
    max-width: 400px;
    height: auto; // The height will adjust automatically to maintain aspect ratio
  }
}