/* Apply box-sizing to all elements */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Header container styling */
.Header-class {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 80px;
  background-color: #0a192f;
  position: fixed;
  width: 100%;
  z-index: 1000;

  .logo {
    img {
      width: 100px;
      height: auto;
    }
  }
}

/* Menu overlay styling */
.menu-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(10, 25, 47, 0.7);
  backdrop-filter: blur(5px);
  z-index: 998;
  transition: opacity 0.3s ease;
  opacity: 0;

  @media (max-width: 768px) {
    &.show {
      display: block;
      opacity: 1;
    }
  }
}

/* Menu styling */
.menu {
  .menu-item {
    display: flex;
    list-style: none;
    gap: 20px;

    li {
      font-size: 1rem;
      font-weight: 400;
      font-family: "SF Mono", "Fira Code", monospace;
      color: #ccd6f6;
      cursor: pointer;
      display: flex;
      align-items: center;
      
      &:hover {
        .menu-item-number, .menu-item-text {
          color: #64ffda;
        }
      }

      .menu-item-number {
        color: #64ffda;
        margin-right: 0.5rem;
        font-size: 0.9rem;
      }

      .menu-item-text {
        transition: color 0.3s ease;
      }
    }
  }
}

/* Resume button styling */
.resume-button {
  border: 2px solid #64ffda;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: transparent;
  color: #64ffda;
  font-family: "SF Mono", "Fira Code", monospace;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;

  &:hover {
    background-color: rgba(100, 255, 218, 0.1);
  }
}

/* Hamburger menu icon styling */
.hamburger {
  display: none;
  font-size: 1.5rem;
  color: #64ffda;
  cursor: pointer;
  z-index: 1001;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .Header-class {
    padding: 0;
    padding-inline: 20px;
    padding-top: 25px;

    .logo {
      position: absolute;
      left: -6px;
    }

    .hamburger {
      display: block;
      margin-left: auto;
    }
  }

  .menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    height: 100vh;
    background-color: #112240;
    padding: 2rem;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    z-index: 999;

    &.show {
      transform: translateX(0);
    }

    .menu-item {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
    }
  }

  /* Apply blur effect to the main content when menu is open */
  body.menu-open {
    overflow: hidden;
  }

  .blur-content {
    transition: filter 0.3s ease;
  }

  body.menu-open .blur-content {
    filter: blur(5px);
  }
}